.btn {
  padding: 20px;
  border: none;
  cursor: pointer;
  font-weight: bold;
  // border-radius: 20px;

  &.btn-primary {
    background-color: $primary-color;
    color: $light;
    transition: all .5s;

    &:hover {
      background-color: lighten($primary-color, 5%);
    }
  }
}